import NavbarComponent from '@/components/NavbarComponent.vue';
import FooterLayout from '@/components/FooterLayout.vue';
export default {
  name: 'home',
  components: {
    NavbarComponent: NavbarComponent,
    FooterLayout: FooterLayout
  },
  data: function data() {
    return {
      flipedDev: false,
      flipedDes: false,
      flipedFront: false
    };
  },
  methods: {
    goTodetail: function goTodetail(proId, name, description) {
      var query = {
        proId: proId,
        name: name,
        description: description
      };
      return this.$router.push({
        name: 'details',
        params: query
      }); // -> /user/123
    }
  }
};